:root{
    --bg-dark:#1A2F3A;

    --logo-height:100px;
    --header-height:100px;
    --aside-widht:225px;
    --footer-height:40px;

    --shadow:
        0 2px 23px 0 rgba(0,0,0,0.1),
        0 2px 49px 0 rgba(0,0,0,0.06);
}

*{
    box-sizing: border-box;
}

/*Layout em grid*/

.app{
    margin: 0px;
    display: grid;
    grid-template-columns: var(--aside-widht) 1fr;
    grid-template-rows: var(--header-height) 1fr var(--footer-height);
    grid-template-areas: 
        "logo header"
        "menu content"
        "menu footer";
    height: 100vh;
    width: 100vw;
    background-color: #f5f5f5;;
}

aside.logo{
    grid-area: logo;
}

header.header{
    grid-area: header;
}
header.user{
    grid-area: user;
}

aside.menu-area{
    grid-area: menu;
}

main.content {
    grid-area: content;
}


footer.footer{
    grid-area: footer;
}


